@import '../../aditional/colors.css';


.list {
  background-color: var(--card-color);
  color:var(--state-1);
  margin: 0;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 20px ;

  box-shadow: 0px 0px 15px -15px var(--shadow-card);
}

.item {
  margin-bottom: 5px;
  font-size: 16px;
}

.item:last-child {
  margin-bottom: 0px;
}
