
.checkbox {
	opacity: 0;
	position: absolute;
    size: 50px;
    weight: 50px;
}

.label {
	background-color: var(--background-color);
	border-radius: 50px;
    border: 1px solid var(--arrow);
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 5px;
	position: relative;
	height: 8px;
	width: 20px;
	transform: scale(1.5);
}

.label .ball {
	background-color: var(--arrow);
	border-radius: 50%;
	position: absolute;
	top: 2px;
	left: 2px;
	height: 14px;
	width: 14px;
	transform: translateX(0px);
	transition: transform 0.2s linear;
}

.checkbox:checked + .label .ball {
	transform: translateX(12px);
}


.fa-moon {
	color: #f1c40f;
}

.fa-sun {
	color: #f39c12;
}